import {StaticBezier} from "../src/static-bezier";

const sb = new StaticBezier([
    [0, 0], [1, 0], [0, 1], [1, 1]
]);


const values = new Array(11).fill([0, 0]).map((v, i) => sb.at(i / 10));


function runTest() {
    let inner = 0;
    for (let i = 0; i < 1e6; i++) {
        if (++inner == 11)
            inner = 0;

        sb.tSearch(values[inner][0], 0);
    }
}

setTimeout(runTest, 500);